.footer {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    z-index: 1;
    padding: 70px 0;
    background-color: rgba($color: #000000, $alpha: 0.06);

    &__list {
        display: flex;
        justify-content: space-between;

        &-item {
            max-width: 420px;
        }
    }

    &__item-title {
        font-family: "NotoSans";
        font-weight: 800;
        font-size: 18px;
        line-height: 25px;
        color: #E8A2A2;
        opacity: 0.8;
        margin-bottom: 50px;
    }

    &__item-text {
        font-family: "NotoSans";
        font-weight: 300;
        font-size: 18px;
        color: #9E9E9E;
        opacity: 0.8;
        margin-bottom: 50px;
    }

    &__item-link {
        font-family: "NotoSans";
        font-style: normal;
        font-weight: 300;
        font-size: 18px;
        color: #9E9E9E;
        opacity: 0.8;

        transition: color .3s ease;

        &:hover {
            color: #E8CCA2;
        }
    }

    &__contacts-link {
        display: block;
        font-family: "NotoSans";
        font-style: normal;
        font-weight: 300;
        font-size: 18px;
        color: #9E9E9E;
        opacity: 0.8;
        transition: color .3s ease;

        &:hover {
            color: #E8CCA2;
        }
    }

    &__socials {
        display: flex;

        li {
            margin-right: 10px;
        }

        &-link {
            display: block;
           
          
            svg {
                width: 22px;
                height: 22px;
                fill: #EAEAEA;
                transition: fill .3s ease ;
            }

            &:hover svg {
                fill: #E8CCA2;
            }
        }
    }
}