.accardion__content {
    max-height: 0;
    opacity: 0;
    overflow: hidden;
    padding: 0 20px;
    will-change: max-height;
    transition: all 0.3s ease-out;
    box-sizing: content-box;
}

.accardion.open .accardion__content {
    opacity: 1;
    padding-bottom: 25px;
}

.accardion.open .accardion__control {
    &::after {
        transform: rotate(0deg);
    }
}
.accardion__control {
    background-color: transparent;
    border: 1px solid rgba(0, 0, 0, 0.14);
    box-sizing: border-box;
    border-radius: 50px;
    font-family: "NotoSans";
    font-weight: 400l;
    font-size: 16px;
    line-height: 22px;
    letter-spacing: 0.03em;
    color: #191916;
    padding: 11px 37px 11px 14px;
    margin-bottom: 25px;
    cursor: pointer;
    position: relative;

    &::after {
        content: "";
        position: absolute;
        top: 17px;
        right: 16px;
        background-image: url("/img/Polygon.svg");
        background-size: cover;
        background-repeat: no-repeat;
        width: 13px;
        height: 10px;
        transform: rotate(180deg);
    }
}

.accardion__content {

    p {
        font-family: "NotoSans";
        font-weight: 400;
        font-size: 16px;
        line-height: 22px;
        letter-spacing: 0.03em;
        color: #191916;

    }
}