.hero {
    background-image: url("/img/firstSC.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: top center;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    position: relative;

    &::before {
        content: "";
        position: absolute;
        right: 42%;
        bottom: 50px;
        z-index: 1;
        background-image: url("/img/Arrow-down.svg");
        background-size: cover;
        background-repeat: no-repeat;
        width: 7px;
        height: 398px;
    }

    &__container {
        min-height: 100vh;
        display: flex;
    }

    &__wrapper {
        display: flex;
        flex-direction: column;
        justify-content: center;
        padding-top: 140px;
    }

    &__title {
        max-width: 1077px;
        font-family: "Rubik";
        font-weight: 400;
        font-size: 72px;
        line-height: 85px;
        color: #EAEAEA;
        margin: 0;
        margin-bottom: 53px;
    }

    &__text {
        max-width: 561px;
        font-family: "NotoSans";
        font-weight: 400;
        font-size: 24px;
        line-height: 41px;
        color: #FFFFFF;
        margin-bottom: 80px;
    }

    
    &__btn {
        display: inline-block;
        font-family: "NotoSans";
        font-weight: 300;
        font-size: 24px;
        line-height: 33px;
        color: #FFFFFF;
        transition: color .3s ease;

        &:hover {
            color: #E8CCA2;
        }

        & + & {
            margin-left: 86px;
        }

        &-order {
            position: relative;

            &::before {
                content: "";
                position: absolute;
                top: -25px;
                left: -37px;
                z-index: 1;
                background-image: url("/img/Ellipse.svg");
                width: 172px;
                height: 85px;    
            }
        }
    }
}

.pink {
    color: #E8A2A2;
}

.blue {
    color: #A2CBE8;
}

.underline {
   text-decoration: underline;
}
