.price {
    background-image: url("/img/four.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: top center;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    padding: 100px 0;

    &__container {
        width: 100%;
        min-height: 100vh;
        display: flex;
    }

    &__wrapper {
        display: flex;
        flex-direction: column;
    }

    &__title {
        font-family: "Rubik";
        font-size: 64px;
        font-weight: 500;
        line-height: 76px;
        margin: 0;
        color: #EAEAEA;
        margin-bottom: 54px;
    }

    &__text {
        max-width: 780px;
        font-family: "NotoSans";
        font-weight: 400;
        font-size: 24px;
        line-height: 33px;
        letter-spacing: 0.03em;
        margin-bottom: 100px;
        color: #EAEAEA;
    }

   

    &__form {
       
        max-width: 960px;
    }

    &__input-wrapper {
        display: flex;
        justify-content: space-between;
        margin-bottom: 100px;
    }

    &__input {
        max-width: 431px;
        width: 100%;
        padding: 20px 10px;
        background-color: transparent;
        border: none;
        border-bottom: 1px solid #8A8A8A ;
        opacity: 1;
        font-family: "NotoSans";
        font-weight: 300;
        font-size: 22px;
        line-height: 30px;
        letter-spacing: 0.03em;
        color: #EAEAEA;
        opacity: 0.5;
    }

    &__form-descr {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__form-text {
        max-width: 625px;
        font-family: "NotoSans";
        font-style: normal;
        font-weight: 300;
        font-size: 18px;
        line-height: 25px;
        letter-spacing: 0.03em;
        color: #EAEAEA;
        opacity: 0.6;
    }
}

.btn__price-form {
    cursor: pointer;
}