.custom.animate-open {
    opacity: 1;
    transform: rotate(180deg);
    transition: transform var(--transition-time), opacity var(--transition-time);
}

.modal {

    &__container {
        position: relative;
        width: 595px;
        margin: 50px auto;
        display: inline-block;
        vertical-align: middle;
        background-color: #fff;
        border-radius: 10px;
        padding: 25px 28px 36px 32px;
        cursor: default;
        text-align: left;
        display: none;
        background: rgba(255, 255, 255, 0.92);
        box-shadow: 0px 80px 40px rgba(0, 0, 0, 0.2);
        backdrop-filter: blur(15px);
        border-radius: 9px;
    }

    &__close {
        background-image: url("/img/close.svg");
        background-repeat: no-repeat;
        background-size: cover;
        right: 20px;
        top: 17px;
        border: none;
        padding: 0;
        width: 26px;
        height: 28px;
    }

    &__title {
        font-family: "Rubik";
        font-style: normal;
        font-weight: 500;
        font-size: 32px;
        line-height: 38px;
        color: #191916;
        margin-bottom: 12px;
    }

    &__text {
        max-width: 469px;
        font-family: "NotoSans";
        font-weight: 400;
        font-size: 16px;
        line-height: 22px;
        letter-spacing: 0.03em;
        color: #191916;
        margin-bottom: 20px;
    }


    &__form {

        input {
            width: 100%;
            font-family: "NotoSans";
            font-style: normal;
            font-weight: 300;
            font-size: 16px;
            line-height: 22px;
            letter-spacing: 0.03em;
            color: #000000;
            border: none;
            opacity: 0.5;
            border-bottom: 1px solid rgba($color: #000000, $alpha: 0.2);
            background-color: transparent;
            padding: 10px 10px;
        }
    }

    &__input-wrapper {
        display: flex;
        justify-content: space-between;
        width: 100%;
        margin-bottom: 10px;
    
        input {
            width: 48%;
        }
    }

    &__form-descr {
        margin-top: 40px;
        font-family: "NotoSans";
        font-style: normal;
        font-weight: 300;
        font-size: 16px;
        line-height: 22px;
        letter-spacing: 0.03em;
        color: #000000;
        opacity: 0.5;
        margin-bottom: 17px;
    
        a {
            text-decoration: underline;
        }
    }

    &__form-btn {
        padding: 11px 18px;
        font-family: "NotoSans";
        font-style: normal;
        font-weight: 300;
        font-size: 16px;
        line-height: 22px;
        letter-spacing: 0.03em;
        color: rgba($color: #000000, $alpha: 0.5);
        border: 1px solid rgba(0, 0, 0, 0.4);
        border-radius: 50px;
    }
}

.modal-second {
    width: 842px;
}

