.order {
    background-image: url("/img/third.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: top center;
    min-height: 100vh;
    display: flex;
    flex-direction: column;

    padding: 100px 0;

    &__container {
        width: 100%;
        min-height: 100vh;
        display: flex;
        padding-left: 160px;
        position: relative;
    }

    &__wrapper {
        display: flex;
        flex-direction: column;
     
    }

    &__title {
        font-family: "Rubik";
        font-size: 64px;
        font-weight: 700;
        line-height: 76px;
        margin: 0;
        color: #EAEAEA;
        margin-bottom: 54px;
    }

    &__text {
        max-width: 689px;
        font-family: "NotoSans";
        font-weight: 400;
        font-size: 24px;
        line-height: 33px;
        letter-spacing: 0.03em;
        margin-bottom: 100px;
        color: #EAEAEA;
    }


    &__link-price {
        font-family: "NotoSans";
        font-weight: 300;
        font-size: 24px;
        line-height: 31px;
        letter-spacing: 0.03em;
        text-decoration-line: underline;
        color: #BFBFBF;
        opacity: 0.5;
        text-decoration: underline;
        z-index: 1;
        position: absolute;
        bottom: 0;
        left: 160px;
    }
}


.tabs {
    
    &__buttons {
        position: relative;

        &::before {
            content: "";
            position: absolute;
            bottom: -10px;
            left: 0;
            width: 100%;
            height: 1px;
            background-color: rgba($color: #fff, $alpha: 0.3); 
        }
    }

    &__btn {
        font-family: "NotoSans";
        font-style: normal;
        font-weight: 500;
        font-size: 30.797px;
        line-height: 42px;
        letter-spacing: 0.03em;
        color: #EAEAEA;
        background-color: transparent;
        border: none;
        position: relative;
        cursor: pointer;

        & + & {
            margin-left: 60px;
        }

        &.active {
                color: #E8CCA2;
            &::before {
                content: "";
                position: absolute;
                bottom: -10px;
                left: 0;
                width: 100%;
                height: 1px;
                background-color: rgba($color: #B6B6B6, $alpha: 0.35);
            }
        }
        
    }

    &__item {
        color: #fff;

        display: none;
        padding-top: 30px;

        &.active {
            display: block;
        }
    }
}


.info__list {
    margin-bottom: 72px;

    &-item {
        font-family: "NotoSans";
        font-style: normal;
        font-weight: 300;
        font-size: 24px;
        line-height: 31px;
        letter-spacing: 0.03em;
        color: #EAEAEA;
        opacity: 0.7;
    }
}



.package {

    &__items {
        display: flex;
        margin-bottom: 40px;
    }

    &__list {
        display: flex;
        align-items: center;
        color: #BFBFBF;

       
    
        span {
            display: block;
            font-family: "NotoSans";
            font-weight: 300;
            font-size: 24px;
            line-height: 31px;
            letter-spacing: 0.03em;
        }
    
        &-item {
    
            & + & {
                margin-left: 20px;
            }
        }
    }

    &__buttons {
        display: flex;
        align-items: center;

        span {
            margin: 0 5px;
            color: #A2CBE8;
        }

        button {
            display: flex;
            align-items: center;
            text-align: center;
            justify-content: center;
            background-color: transparent;
            color: #A2CBE8;
            border-radius: 50px;
            border: 1px solid #A2CBE8;
            width: 20px;
            height: 20px;
        }
    }

   
}

.border {
    padding: 0 35px;
    position: relative;

    &::before {
        content: "";
        position: absolute;
        top: 0;
        left: 50%;
        transform: translateX(-50%);
        width: 1px;
        height: 100%;
        background-color: #8A8A8A;
    }
}

.btn__primary {
    padding: 13px 20px;
    border: 1px solid #8A8A8A;
    border-radius: 50px;
    font-family: "NotoSans";
    font-style: normal;
    font-weight: 300;
    font-size: 24px;
    line-height: 31px;
    letter-spacing: 0.03em;
    color: #BFBFBF;
    background-color: transparent;
    transition: color .3s ease, border .3s ease;
    cursor: pointer;

    &:hover {
        color: #E8CCA2;
        border: 1px solid #E8CCA2;

    }
}