.header {
    position: absolute;
    top: 116px;
    left: 0;
    z-index: 1;
    width: 100%;

    &__container {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
}

.nav {
   position: relative;
   
   &::before {
       content: "";
       position: absolute;
       bottom: -10px;
       left: 0;
       width: 100%;
       height: 1px;
       background-color: rgba($color: #fff, $alpha: 0.3); 
   }
    
    &__list {
        display: flex;
        padding: 0 20px;
    }

    &__item {
        & + & {
            margin-left: 60px;
        }
    }

    &__link {
        font-family: "NotoSans" ;
        font-size: 18px;
        line-height: 25px;
        font-weight: 600;
        letter-spacing: 0.09em;
        color: #FFFFFF;
        transition: color .3s ease;
        position: relative;

        &:hover {
            color: #E8CCA2;

            &::before {
                transition: all .3s ease;
                width: 100%;
            }
           
        }

        &::before {
            content: "";
            position: absolute;
            bottom: -11px;
            left: 0;
            width: 0%;
            height: 1px;
            background-color: rgba($color: #B6B6B6, $alpha: 0.35);
        }
    }

}











