.benefits {
    background-image: url("/img/secondSC.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    min-height: 100vh;
    padding: 70px 0;

    &__container {
        min-height: 100vh;
        display: flex;
    }

    &__wrapper {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        padding-left: 233px;
    }

    &__list {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        height: 100%;

        &-item {
            position: relative;
        
            &:nth-child(1) {
                &::before {
                    content: "";
                    position: absolute;
                    top: -194px;
                    left: 0;
                    z-index: 1;
                    background-image: url("/img/circle-one.svg");
                    background-size: cover;
                    background-repeat: no-repeat;
                    width: 453px;
                    height: 555px;
    
                }
          
            }

            &:nth-child(2) {

                &::before {
                    content: "";
                    position: absolute;
                    top: -15px;
                    left: 0;
                    z-index: 1;
                    background-image: url("/img/circle-two.svg");
                    background-size: cover;
                    background-repeat: no-repeat;
                    width: 453px;
                    height: 423px;
                }
          
            }

            &:nth-child(3) {

                &::before {
                    content: "";
                    position: absolute;
                    top: 0;
                    left: 0;
                    z-index: 1;
                    background-image: url("/img/circle-three.svg");
                    background-size: cover;
                    background-repeat: no-repeat;
                    width: 453px;
                    height: 453px;
                }
          
            }
        }
    }

    &__item-title {
        font-family: "Rubik";
        font-size: 64px;
        font-weight: 700;
        line-height: 76px;
        color: #EAEAEA;
        margin-bottom: 25px;
    }

    &__item-text {
        max-width: 640px;
        font-family: "NotoSans";
        font-size: 22px;
        font-weight: 400;
        line-height: 30px;
        letter-spacing: 0.03em;
        color: #EAEAEA;
    }
}